<template name="components">
	<view class="page">
		<cu-custom bgColor="bg-gradual-black text-white"  :isBack="true"><block slot="content">人生答案之书</block></cu-custom>
		<image src="../../static/answer-book-tips.png" mode="widthFix" class="bg-tips"></image>
		<image src="../../static/answer-book-light.png" mode="widthFix" class="bg-light"></image>
		<!-- <navigator url="../answer/home" open-type="navigate"> -->
			<image src="../../static/answer-book.png" mode="widthFix" class="bg-book" @click="goAnswer()"></image>
		<!-- </navigator> -->
	</view>
</template>

<script>
	export default {

		data() {
			return {

			};
		},
		created() {},
		methods: {
			goAnswer(){
				uni.showLoading({
					title:'答案马上来'
				})
				
				setTimeout(() => {
					uni.navigateTo({
					　　url:'../answer/home', //跳转路径
					　　animationType:'pop-out', //跳转动画  默认值为 pop-in,  可以的值 pop-in、pop-out、none、auto 等。。。
					　　animationDuration:200,  //设置窗口显示/关闭动画的持续时间，单位为 ms
					})
					
					uni.hideLoading()
				},1000)
				
			}
		}
	}
</script>

<style lang="less">
	.page {
		height: 100vh;
		padding-bottom: 150rpx;
		background: rgb(30, 26, 25);

		.bg-light {
			position: absolute;
			top: 30%;
			left: 5%;
			width: 90%;
		}

		.bg-book {
			position: absolute;
			top: 35%;
			left: 15%;
			width: 70%;
			z-index: 2;
		}
		
		.bg-tips{
			position: absolute;
			top: 20%;
			left: 30%;
			width: 40%;
			z-index: 2;
		}
	}
</style>
